//ãæ³¨æãæ¼é³è¾å¥æ¶ maxLength éå¶å¨ CompositionEnd äºä»¶ä¸­å¤ç

import { Editor, Node } from 'slate'
import { IDomEditor, DomEditor } from '../..'
import { IGNORE_TAGS } from '../../constants'
import { NodeType } from '../../utils/dom'

export const withMaxLength = <T extends Editor>(editor: T) => {
  const e = editor as T & IDomEditor
  const { insertText, insertNode, insertFragment, dangerouslyInsertHtml } = e

  // å¤ç text
  e.insertText = (text: string) => {
    const { maxLength } = e.getConfig()
    if (!maxLength) {
      insertText(text)
      return
    }

    const leftLength = DomEditor.getLeftLengthOfMaxLength(e)
    if (leftLength <= 0) {
      // å·²ç»è§¦å maxLength ï¼ä¸åè¾å¥æå­
      return
    }

    if (leftLength < text.length) {
      // å©ä½é¿åº¦å°äº text é¿åº¦ï¼åæªå text
      insertText(text.slice(0, leftLength))
      return
    }

    insertText(text)
  }

  // å¤ç node
  e.insertNode = (node: Node) => {
    const { maxLength } = e.getConfig()
    if (!maxLength) {
      insertNode(node)
      return
    }

    const leftLength = DomEditor.getLeftLengthOfMaxLength(e)
    if (leftLength <= 0) {
      // å·²ç»è§¦å maxLength ï¼ä¸åæå¥
      return
    }

    const text = Node.string(node)
    if (leftLength < text.length) {
      // å©ä½é¿åº¦ï¼ä¸å¤ node text é¿åº¦ï¼ä¸åæå¥
      return
    }

    insertNode(node)
  }

  // å¤ç fragment
  e.insertFragment = (fragment: Node[]) => {
    const { maxLength } = e.getConfig()
    if (!maxLength) {
      // æ  maxLength
      insertFragment(fragment)
      return
    }

    // åªæä¸ä¸ª node æ¶ï¼ä½¿ç¨ insertFragment ï¼é²æ­¢æ¢è¡
    if (fragment.length === 1) {
      const node = fragment[0]
      const leftLength = DomEditor.getLeftLengthOfMaxLength(e)
      const text = Node.string(node)

      if (leftLength < text.length) {
        // å·²ç»è§¦å maxLength ï¼ä¸åæå¥
        return
      }

      insertFragment(fragment)
      return
    }
    // æ maxLength ï¼ååå«æå¥ node
    fragment.forEach(n => {
      e.insertNode(n) //ãæ³¨æãè¿éå¿é¡»ä½¿ç¨ `e.insertNode` ï¼èä¸æ¯ insertNode
    })
  }

  e.dangerouslyInsertHtml = (html: string = '', isRecursive = false) => {
    if (!html) return

    const { maxLength } = e.getConfig()
    if (!maxLength) {
      // æ  maxLength
      dangerouslyInsertHtml(html, isRecursive)
      return
    }
    const leftLength = DomEditor.getLeftLengthOfMaxLength(e)
    if (leftLength <= 0) {
      // å·²ç»è§¦å maxLength ï¼ä¸åè¾å¥æå­
      return
    }

    // ------------- æ html è½¬æ¢ä¸º DOM nodes -------------
    const div = document.createElement('div')
    div.innerHTML = html
    const text = Array.from(div.childNodes).reduce<string>((acc, node) => {
      const { nodeType, nodeName } = node
      if (!node) {
        return acc
      }
      // Text Node
      if (nodeType === NodeType.TEXT_NODE) return acc + (node.textContent || '')

      // Element Node
      if (nodeType === NodeType.ELEMENT_NODE) {
        // è¿æ»¤æå¿½ç¥ç tag
        if (IGNORE_TAGS.has(nodeName.toLowerCase())) return acc
        else return acc + (node.textContent || '')
      }
      return acc
    }, '')

    if (leftLength < text.length) {
      return
    }

    dangerouslyInsertHtml(html, isRecursive)
  }

  return e // è¿å editor å®ä¾
}
